.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

.gallery {
  margin-bottom: 1.5em;
}

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%;
}
.gallery-columns-2 .gallery-item {
  max-width: 50%;
}
.gallery-columns-3 .gallery-item {
  max-width: 33.33%;
}
.gallery-columns-4 .gallery-item {
  max-width: 25%;
}
.gallery-columns-5 .gallery-item {
  max-width: 20%;
}
.gallery-columns-6 .gallery-item {
  max-width: 16.66%;
}
.gallery-columns-7 .gallery-item {
  max-width: 14.28%;
}
.gallery-columns-8 .gallery-item {
  max-width: 12.5%;
}
.gallery-columns-9 .gallery-item {
  max-width: 11.11%;
}

.gallery-caption {
  display: block;
}

.navbar-nav {
  white-space: nowrap;
  overflow-x: auto;
  flex-direction: row;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
  padding: 0 0.25rem;
}

.navbar-nav li {
  font-family: "DIN-Regular", Arial, sans-serif;
}

.navbar-nav::-webkit-scrollbar {
  display: none;
}

.navbar-nav li a, .navbar-nav li.menu-item-email {
  line-height: 19.33px;
  letter-spacing: 1.6px;
  padding: 1rem;
}

.navbar-nav li.menu-item-email {
  color: #777777;
}

.navbar-nav li.menu-item-email svg {
  margin-right: 11px;
}

#main-nav {
  border-bottom: 0.5px solid #848484;
  transition: transform 0.1s ease-out;
  will-change: transform;
}

body.blog #main-nav {
  border: none;
}

.custom-logo-link {
  padding-inline-start: 1.25rem;
  display: block;
  padding-bottom: 1.25rem;
}

header {
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

header div.container-fluid, header div.container-sm, header div.container-md, header div.container-lg, header div.container-xl, header div.container-xxl {
  padding: 1.875rem 0 0;
}

body.blog {
  margin-top: 182.93px;
}

@media only screen and (min-width: 1184px) {
  body.blog {
    margin-top: 0px;
  }
  .custom-logo-link {
    padding-bottom: 3.375em;
    padding-inline-start: 0;
  }
